.customer-info {
    .customer-info-username {
        margin-bottom: 5px;
        font-family: HelveticaNeue;
        font-size: 13px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #323232;
        text-transform: uppercase;
    }
    .customer-info-agency {
        color: #ccc;
        margin-bottom: 5px;
    }
    .customer-info-phone {
        margin-bottom: 10px;
    }
    .customer-info-group-btn {
        margin-bottom: 10px;
    }
    .customer-info-btn {
        display: inline-block;
        padding: 5px;
        margin-right: 5px;
        color: #000;
        border: 1px solid #00a232;
        border-radius: 5px;

        &:hover {
            text-decoration: none;
        }
    }
    .customer-info-group-item {
        border-top: 1px solid #ccc;
    }
    .customer-info-item {
        padding: 15px 0;
        border-top: 1px solid #ccc;
    }
    .customer-info-status {
        margin-left: 5px;
        color:#737171;
    }
    .head-collapse {
        padding: 10px 0;
    }
    .accordion-toggle:after {
        /* symbol for "opening" panels */
        font-family: 'Glyphicons Halflings';  /* essential for enabling glyphicon */
        content: "\e114";    /* adjust as needed, taken from bootstrap.css */
        float: right;        /* adjust as needed */
        color: #737171;         /* adjust as needed */
    }
    .accordion-toggle.collapsed:after {
        /* symbol for "collapsed" panels */
        content: "\e080";    /* adjust as needed, taken from bootstrap.css */
    }

    .customer-info-search{
        margin: 12px 0px;
    }
}